.title {
  margin: 0;

  font-weight: 600;
  font-size: 54px;
  line-height: normal;
  font-family: $ff-alumni-sans;
  letter-spacing: 1.44px;

  @include vp-1199 {
    font-size: 42px;
    letter-spacing: 1.26px;
  }

  @include vp-767 {
    font-size: 32px;
    letter-spacing: 0.96px;
  }

  &--white {
    color: $color-default-white;
  }
}
